import React from 'react';

import {initializeScene, drawScene} from '../gl-code/scene.js';


class FunctionPlot extends React.PureComponent {
  constructor(props) {
    super(props);

    this.gl = null;

    this.variables = this.props.variables;
    this.variableLocations = {};
    this.initialized = false;
  }

  handleWheelEvent(event) {
    const scrollAmount = event.deltaY;
    const delta_log_scale = 0.05;

    if (scrollAmount > 0) {
      this.variables.log_scale -= delta_log_scale;
    } else {
      this.variables.log_scale += delta_log_scale;
    }

    this.updatePlot();
  }

  handleVariableUpdate(variables) {
    for (const [name, value] of Object.entries(variables)) {
      this.variables[name] = value;
    }

    this.updatePlot();
  }

  componentDidMount() {
    const canvas = this.refs.canvas;
    canvas.width = canvas.offsetWidth;
    canvas.height = canvas.offsetHeight;

    this.gl = canvas.getContext('webgl');

    if (this.gl === null) {
      console.log('Unable to initialize WebGL.');
      return;
    }
    this.componentDidUpdate();
  }

  componentDidUpdate() {
    this.initializePlot();
    this.updatePlot();
    this.props.onError(!this.initialized);
  }

  initializePlot() {
    this.variableLocations = initializeScene(
      this.gl,
      this.props.expression,
      Object.keys(this.variables)
    );

    this.initialized = (this.variableLocations !== null);
  }

  updatePlot() {
    if (!this.initialized) {return;}

    const variableAssignments = {};
    for (const [name, value] of Object.entries(this.variables)) {
      variableAssignments[name] = [this.variableLocations[name], value];
    }
    drawScene(this.gl, variableAssignments);
  }

  render() {
    return (
      <div id='function-plot'>
	<canvas
	  ref='canvas'
	  onWheel={(event) => this.handleWheelEvent(event)}
	/>
      </div>
    );
  }
}

export default FunctionPlot;



